/* Base styles */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  
  body {
    background-color: #f5f5f5;
  }
  
  /* Navbar styles */
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    background-color: #d72121;
    color: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .navbar-brand {
    font-size: 1.8rem;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
  }
  
  .navbar a {
    color: #fff;
    text-decoration: none;
    margin-left: 1rem;
  }
  
  .navbar a:hover {
    color: #ddd;
  }
  
  /* Container styles */
  .container {
    max-width: 700px;
    margin: 4rem auto;
    padding: 3rem;
    border-radius: 5px;
    background-color: #30d59b;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .container h3 {
    margin-bottom: 1.5rem;
    color: #344a49;
  }
  
  .container p {
    color: #6c757d;
    line-height: 1.5;
  }
  
  .container button {
    background-color: #3cd013;
    color: #1512ce;
    border: none;
    padding: 1rem 2rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .container button:hover {
    background-color: #8c8e20;
  }